














import Vue from "vue";
import { mapActions, mapGetters } from "vuex";
import { BaseContainer, BaseColumn, BaseRow } from "@/components";

interface Lang {
    id: number;
    code: string;
    name: string;
}

const langs: Lang[] = [
    {
        id: 1,
        code: "en",
        name: "English",
    },
    {
        id: 2,
        code: "fr",
        name: "Fran√ßais",
    },
];

export default Vue.extend({
    components: {
        BaseContainer,
        BaseColumn,
        BaseRow,
    },
    created() {
        this.selectedLanguage = this.language;
    },
    data() {
        return {
            selectedLanguage: "en",
            langs,
        };
    },
    computed: {
        ...mapGetters(["language"]),
    },
    methods: {
        ...mapActions(["setLanguage"]),
        async changeLanguage() {
            await this.setLanguage(this.selectedLanguage);
        },
    },
});
